.setting__display .ant-radio-group {
  width: 612px; }

.setting__display .theme .ant-radio-wrapper {
  position: relative;
  padding-left: 62px;
  display: inline-block;
  width: 152px;
  height: 120px; }
  .setting__display .theme .ant-radio-wrapper span:last-child {
    position: absolute;
    top: 0;
    left: -12px; }
  .setting__display .theme .ant-radio-wrapper .ant-radio {
    margin-top: 94px; }
    .setting__display .theme .ant-radio-wrapper .ant-radio::after {
