.settings__general .ant-card {
  width: 400px;
  font-size: 12px;
  border-radius: 24px;
  box-shadow: 0 1px 5px #E9E9E9; }

.settings__general .user_base_info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px; }
  .settings__general .user_base_info h3 {
    font-size: 28px;
    margin-bottom: 8px; }
    .settings__general .user_base_info h3 span {
      display: inline-block;
      width: 16px;
      height: 16px;
      background-size: 100%;
      margin-left: 8px; }
      .settings__general .user_base_info h3 span.male {
        background-image: url(/static/files/1f89617c2c4f4a8f840482715bd48317.svg); }
      .settings__general .user_base_info h3 span.female {
        background-image: url(/static/files/0d0507d5eb35742595b5e0bcc92ab9a3.svg); }
  .settings__general .user_base_info p {
    font-size: 14px;
    margin-bottom: 0; }
  .settings__general .user_base_info img {
    width: 100%;
    border-radius: 50%; }
  .settings__general .user_base_info .profile-picture {
    width: 60px;
    height: 60px;
    position: relative; }
    .settings__general .user_base_info .profile-picture input {
      position: absolute;
      left: 0;
      top: 0;
      width: 60px;
      height: 60px;
      cursor: pointer;
      outline: none;
      z-index: 2; }
      .settings__general .user_base_info .profile-picture input[disabled] {
        cursor: not-allowed; }
    .settings__general .user_base_info .profile-picture span {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 16px;
      height: 16px;
      background: url(/static/files/f6ba15b8ce54b2afc6b64fc782f6de23.svg);
      background-size: 100%;
      z-index: 1; }

.settings__general .ant-col label {
  color: #999;
  margin-right: 10px;
  font-weight: normal; }

.settings__general .ant-col span {
  display: inline-block;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 120px;
  vertical-align: middle; }
